import { EventEmitter } from 'events'
import * as React from 'react'
import * as ReactDOM from 'react-dom'
import App from "../span-charm-react/App"; // span-charm-react
export default class DisplayManager extends EventEmitter {
    constructor() {
        super()
    }
    public init() {
        window.addEventListener("mouseup", (e) => {
            console.log('[DEBG]', e.which);
            let bag = {
                type: 'MOUSE_CLICK',
                value: e.which
            }
            this.emit('act', bag)
        })
        window.addEventListener("keydown", (e) => {
            console.log('[DEBG]', e.key);
        })
        window.addEventListener("keyup", (e) => {
            console.log('[DEBG]', e.key);
            let bag = {
                type: 'keyup',
                value: e.which
            }
            this.emit('act', bag)

        })
    }
    public start() { }
    public send(bag: any) {
        let msg = JSON.stringify(bag)
        // console.log('[DEBG]', msg); // 
        this.emit('send', msg)
    }
    public recv(msg: any) {
        let bag = JSON.parse(msg)
        // console.log('[DEBG]', msg); // 
        this.parse(bag)
    }
    private parse(bag: any) { }
    onChange(data) {
        ReactDOM.render(
            <App data={data} />,
            document.getElementById('root')
        )
    }
}